import "../../document.css";
import React from "react";
export default () => (
  <div style={{ padding: "1rem" }}>
    <p>Effective Date: May 5, 2020.</p>

    <h3>Privacy Statement</h3>

    <h4 id="overview">Overview</h4>

    <p>
      Local-Currency.com has developed this Privacy Statement to explain how
      Local-Currency.com, as a Data Controller, may collect, retain, process,
      share and transfer your Personal Data when you visit our Sites or use our
      Services. This Privacy Statement applies to your Personal Data when you
      visit Sites or use Services, and does not apply to online websites or
      services that we do not own or control, including websites or services of
      other Local-Currency.com Users.
    </p>

    <p>
      This Privacy Statement is designed to help you obtain information about
      our privacy practices and to help you understand your privacy choices when
      you use our Sites and Services. This Privacy Statement may be supplemented
      with additional notices depending on the Sites and Services concerned.
      Supplementary information can be found in the Statement on Cookies and
      Tracking Technologies.
    </p>

    <p>
      We have defined some terms that we use throughout the Privacy Statement.
    </p>

    <h4>What Personal Data Do We Collect?</h4>

    <p>
      We collect Personal Data about you when you visit our Sites or use our
      Services, including the following:
    </p>

    <ul>
      <li>
        <b>Registration and use information – </b>When you register to use our
        Services by establishing an Account, we will collect Personal Data as
        necessary to offer and fulfil the Services you request. Depending on the
        Services you choose, we may require you to provide us with your name,
        postal address, telephone number, email address and identification
        information to establish an Account. We may require you to provide us
        with additional Personal Data as you use our Services.
      </li>

      <li>
        <b>Transaction and experience information – </b>When you use our
        Services or access our Sites, for example, to make purchases from
        merchants, to receive transfers, to process payments, or to send
        transfers to other users, we collect information about the transaction,
        as well as other information associated with the transaction such as
        amount sent or requested, amount paid for products or services, merchant
        information, including information about any funding instruments used to
        complete the transaction, Device Information, Technical Usage Data, and
        Geolocation Information.
      </li>

      <li>
        <b>Participant Personal Data – </b>When you use our Services or access
        our Sites, we collect Personal Data you provide us about the other
        participants associated with the transaction.
        <ul>
          <li>
            <b>Send or receive transfers: </b>When you send or receive transfers
            through the Services, we collect Personal Data such as name, postal
            address, telephone number, and financial account information about
            the participant who is receiving transfer from you or sending
            transfer to you. The extent of Personal Data required about a
            participant may vary depending on the Services you are using to send
            or request transfers.
          </li>
          <li>
            <b>Pay or request someone else to pay a bill: </b>If you use our
            Services to pay a bill for the benefit of someone else, or if you
            request a User to pay a bill for you, we collect Personal Data from
            you about the account holder such as name, postal address, telephone
            number, email address, and account number of the bill that you
            intend to pay or request to be paid.
          </li>
          <li>
            <b>Add value to your accounts: </b>If you use our Services to add
            value to your Account or any other account you may have, or if you
            ask a User to add value to any of these accounts, we may collect
            Personal Data from you about the other party, or from the other
            party about you to facilitate the request.
          </li>
        </ul>
      </li>
      <li>
        <b>Personal Data about your friends and contacts – </b>It may be easier
        for us to help you transact with your friends and contacts if you choose
        to connect your contact list information with your Account. If you
        establish an account connection between your device or a social media
        platform and your Account, we will use your contact list information
        (such as name, address, email address) to improve your experience when
        you use our Services.
      </li>
      <li>
        <b>
          Personal data that you choose to provide us to obtain additional
          Services or specific online Services –{" "}
        </b>
        If you request or participate in an optional Site feature, or request
        enhanced Services or other elective functionality, we may collect
        additional information from you. We will provide you with a separate
        notice at the time of collection, if the use of that Personal Data
        differs from the uses disclosed in this Privacy Statement.
      </li>
      <li>
        <b>
          Personal Data about you if you use the Service "Checkout without a
          Local-Currency.com Account" –{" "}
        </b>
        Certain limited Services are available without being required to log in
        to or establish an Account, also referred to as Checkout without a
        Local-Currency.com Account. We will collect Personal Data, information
        about any funding instrument used, Device Information, Technical Usage
        Data, and Geolocation Information as necessary to complete the
        transaction. If you are an Account holder and choose to engage in a
        Checkout without a Local-Currency.com Account transaction, we may
        collect information about the transaction and associate it with your
        Account as part of our compliance and analytics operations. If you are
        not an Account holder and choose to engage in a Checkout without a
        Local-Currency.com Account transaction, we will collect and store all
        information you provide and use such information in accordance with this
        Privacy Statement.
      </li>
      <li>
        <b>Personal Data about you from third-party sources – </b>We obtain
        information from third-party sources such as merchants, data providers,
        and credit bureaus, where permitted by law.
      </li>
      <li>
        <b>
          Other information we collect related to your use of our Sites or
          Services –{" "}
        </b>
        We may collect additional information from or about you when you
        communicate with us, contact our customer support teams or respond to a
        survey.
      </li>
    </ul>

    <h4>Why Do We Retain Personal Data?</h4>

    <p>
      We retain Personal Data in an identifiable format for the least amount of
      time necessary to fulfill our legal or regulatory obligations and for our
      business purposes. We may retain Personal Data for longer periods than
      required by law if it is in our legitimate business interests and not
      prohibited by law. If your Account is closed, we may take steps to mask
      Personal Data and other information, but we reserve our ability to retain
      and access the data for so long as required to comply with applicable
      laws. We will continue to use and disclose such Personal data in
      accordance with this Privacy Statement.
    </p>

    <p>
      The cookies we use have defined expiration times; unless you visit our
      Sites or use our Services within that time, the cookies are automatically
      disabled and retained data is deleted.
    </p>
  </div>
);
